body :global{--colorThemeBgDark: rgb(245, 245, 245)}body :global{--colorThemeBgDark: rgb(245, 245, 245)}.wrapper{position:relative;display:flex;flex-direction:column}.label{font-family:"Roboto",Arial,sans-serif;font-size:1.4rem;font-weight:400;line-height:1.6rem;color:#979797;display:inline-block;margin-bottom:.7rem;opacity:.7}.input{margin:0;padding:0;border:none;box-shadow:none;font-family:"Roboto",Arial,sans-serif;font-size:1.6rem;font-weight:400;line-height:1.9rem;color:#535353;padding:1.4rem 1.5rem .7rem 1.5rem;height:4.1rem;border:1px solid #979797;border-radius:1rem;transition-duration:.3s}.input:hover,.input:focus{border-color:#081d47}.input.inputError,.input.inputError:hover,.input.inputError:focus{border-color:#ff3737}.inputDisabled,.inputDisabled:hover,.inputDisabled:focus{background-color:#d4d4d4;border-color:#c3c3c3;pointer-events:none}.wrapperCash{position:relative}.wrapperCash::before{font-family:"Roboto",Arial,sans-serif;font-size:1.6rem;font-weight:400;line-height:1.9rem;color:#535353;content:"€";position:absolute;bottom:1.5rem;left:2rem}.wrapperCash .input{padding-left:3.5rem}.inputSearch{padding-right:4rem}.icon{position:absolute;top:calc(50% - 2rem / 2);right:1.5rem;width:2rem;fill:#535353}