@import "../../../styles/variables";
@import "../../../styles/mixins";

.cell {
  @include tableCell($colorRegular, 400);

  margin: 0;
  padding: 0;
  max-height: 100%;
  text-transform: none;
  transition-duration: $transitionDuration;
  transition-property: text-decoration;

  &:not(.cellOrderStatus) {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;

    & > * {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
}

.amountDebt {
  color: $colorError;
}

.link {
  @include tableCell($colorRegular, 400);

  &:hover {
    text-decoration: underline;
  }
}
